/* Основное изображение для кликов */
.click-target {
  width: 400px; /* Установите желаемый размер */
  height: auto; /* Автоматически подстраивает высоту */
}

/* Бонусные изображения - в два раза меньше */
.bonus-item {
  width: 100px; /* Половина размера основного изображения */
  height: auto;
}

/* Общие стили для основного контейнера */
.App {
  text-align: center;
  font-family: Arial, sans-serif;
  color: #ecf0f1;
  background-color: #2c3e50;
  height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Адаптация для мобильных устройств */
@media (max-width: 768px) {
  .click-target {
    width: 50vw; /* Использует 50% ширины экрана на мобильных */
  }

  .bonus-item {
    width: 25vw; /* Половина ширины основного изображения на мобильных */
  }
}
